// SB Menu Variables:
// --sb-menu-link-text-color - Menu item link color
// --sb-menu-link-text-hover-color - Menu item link hover color

// TODO: Possibly refactor into VerticalMenu & HorizontalMenu - benefit easier to maintain. Refactor with cleaner classnames.

.sb-menu {
  color: var(--sb-menu-link-text-color);
  font-family: var(--sbt-font-stack-alt);
  list-style: none;
  margin: 0;
  padding: 0;
}

.sb-menu_horizontal {
  align-items: center;
  display: flex;
  justify-content: space-evenly;

  li {
    flex: 1;
    margin: 0;
    text-align: center;

    a,
    span {
      display: inline-block;
      padding: 0.5rem 0;
      width: 100%;
    }
  }

  .sb-menu__menu-item {
    a {
      border-bottom: 3px solid transparent;
    }
  }

  .sb-menu__menu-item_active {
    a {
      border-bottom: 3px solid var(--sbt-theme-color-90);
      color: var(--sbt-theme-color-90);
    }
  }
}

.sb-menu_vertical {
  display: block;
  overflow-x: hidden;

  li {
    height: auto;
    margin: 0;
    text-align: left;

    a,
    span {
      display: inline-block;
      padding: 0.25rem;
      width: 100%;

      &.sb-menu__menu-item__toggle-target {
        display: flex;
      }
    }
  }

  .sb-menu__menu-item {
    a {
      padding: 0.25rem;
    }
  }

  .sb-menu__menu-item__toggle-target {
    padding: 0.25rem;
  }
}

.sb-menu_horizontal .sb-menu_vertical.sb-menu__sub-menu {
  display: block;

  li {
    height: auto;
    margin: 0;
    text-align: left;
  }

  a,
  span {
    display: inline-block;
    padding: 0.25rem;
    width: 100%;

    &.sb-menu__menu-item__toggle-target {
      display: flex;
    }
  }
}

.sb-menu__menu-item {
  a {
    color: var(--sb-menu-link-text-color, var(--sbt-theme-color-70));
    cursor: pointer;
    font-weight: var(--sbt-font-weight-60);
    outline: none;
    text-decoration: none;

    &:hover {
      color: var(--sb-menu-link-text-hover-color, var(--sbt-theme-color-90));
      text-decoration: none;
    }
  }
}

.sb-menu__menu-item_active {
  a {
    border-bottom: 3px solid var(--sbt-theme-color-90);
    color: var(--sbt-theme-color-90);
  }
}

.sb-menu__menu-item_open {
  .sb-menu__sub-menu {
    display: block;
  }

  .sb-menu__menu-item__toggle-target i {
    transform: rotate(180deg);
  }
}

.sb-menu__menu-item_has-toggle {
  position: relative;
}

.sb-menu__menu-item__toggle-target {
  align-items: center;
  color: var(--sb-menu-link-text-color, var(--sbt-theme-color-70));
  cursor: pointer;
  display: flex;
  font-weight: var(--sbt-font-weight-60);
  outline: none;
  text-decoration: none;

  span {
    flex: 1;
  }

  i {
    flex: 0;
    margin: 0 0.25rem;
    transition: 0.15s;
  }

  &:hover {
    color: var(--sb-menu-link-text-hover-color, var(--sbt-theme-color-90));
  }
}

.sb-menu__menu-item__label {
  font-size: var(--sbt-font-size-30);
  font-weight: var(--sbt-font-weight-60);
  padding: 0.5rem;
}

.sb-menu__sub-menu {
  display: none;
  margin: 0;
  width: 100%;

  .sb-menu__menu-item {
    a {
      font-size: var(--sbt-font-size-25);
      padding-left: 1rem;
    }
  }

  .sb-menu__menu-item_active {
    a {
      border-bottom: initial;
      color: initial;
    }
  }
}
